<template>
	<view class="content">
		<scroll-view id="tab-bar" class="uni-swiper-tab displayDiv" :class="showComponent?'topnav':''" scroll-x >
				 <view class="swiper-tab-list" v-for="(tab,index) in tabBars" :key="index"  :id="tab.value"
				  :data-current="index" @click="tapTab(index)">
					<view class="tab-content" v-text="tab.name" :class="[tabIndex==index ? 'active' : '']"></view>
				  </view>
			</scroll-view>
			<scroll-view id="tab-bar2" class="uni-swiper-tab" scroll-x >
				 <view class="swiper-tab-list" v-for="(tab,index) in tabBars" :key="index" :id="tab.value"
				  :data-current="index" @click="tapTab(index)">
					<view class="tab-content" v-text="tab.name" :class="[tabIndex==index ? 'active' : '']"></view>
				  </view>
			</scroll-view>
			
		</scroll-view>
	</view>
</template>

<script>
import cSwiper from '@/components/comp-swiper.vue';
import nList from '@/components/NewsList.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		cSwiper,
		nList,
		uniLoadMore
	},
	data() {
		return {
			loadingText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			tabBars: [
				{
					name: '案例',
					value: 'demo'
				},
				{
					name: '新闻',
					value: 'news'
				}
			],
			scrollComponents: '#tab-bar2', // tab 容器 计算距离顶部距离使用
			showComponent:false, // 是否显示
			scrollTop: 0,  // scroll-view组件 距离顶部距离，点击tab时间置顶使用
			tabIndex: 0,  // tab组件选项卡事件参数
			dataList: [
				{
					img: '../../static/logo.png',
					title: '幻灯1',
					info: '简介',
					another: '额外',
					id: 1
				},
				{
					img: '../../static/logo.png',
					title: '幻灯幻灯幻灯幻灯幻灯幻灯幻灯幻灯幻灯幻灯2',
					info:
						'简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介简介',
					another: '额外',
					id: 1
				},
				{
					img: '../../static/logo.png',
					title: '幻灯3',
					info: '简介',
					another: '额外',
					id: 1
				},
				{
					img: '../../static/logo.png',
					title: '幻灯4',
					info: '简介',
					another: '额外',
					id: 1
				},
				{
					img: '../../static/logo.png',
					title: '幻灯5',
					info: '简介',
					another: '额外',
					id: 1
				},
				{
					img: '../../static/logo.png',
					title: '幻灯6',
					info: '简介',
					another: '额外',
					id: 1
				}
			],
			// 轮播图组件参数
			componentSwiper:{
				indicatorDots: false,
				autoplay: true,
				titleShow: true,
				interval: 2000,
				duration: 500,
				goUrl: '',
			},
			top: 0, // 顶部监听事件使用
			NewsGoUrl:'',// 列表点击链接
			loadingType: 0 // 加载更多状态
		};
	},
	onLoad() {
		// console.log(JSON.stringify(this.dataList));
	},
	methods: {
		// 点击事件
		tap(o) {
			let _this = this;
			if (_this.goUrl != '') {
				uni.navigateTo({
					url: _this.goUrl + o
				});
			}
		},
		// 监听滑动事件
		scrollTopFun(e) {
			let _this = this;
			_this.top = e.detail.scrollTop;
			_this.getScrollTop()
		},
		// 计算距离
		getScrollTop() {
			let view = uni.createSelectorQuery().select(this.scrollComponents);
			view.boundingClientRect(data => {
				// data.top 节点离页面顶部的距离为;
				if(data.top<=0){
					this.showComponent=true
				}else{
					this.showComponent=false
				}
			}).exec();
		},
		// 滑动底部加载
		loadMore(e) {
			let _this = this;
			let list = _this.dataList;
			_this.loadingType = 1;
			setTimeout(() => {
				let li = {
					img: '../../static/logo.png',
					title: '幻灯' + e.timeStamp.toString(),
					info: '简介',
					another: '额外',
					id: e.timeStamp.toString()
				};
				list.push(li);
				_this.loadingType = 0;
			}, 1200);
		},
		// tabbar 点击事件
		async tapTab(e) {
			if (this.tabIndex === e) {
				return false;
			} else {
				uni.showLoading({
					title: '加载中'
				});
				let datas = await this.getDataList(e); //异步请求数据
				this.tabIndex = datas;
				this.scrollTop = 0;
			}
		},
		getDataList(e) {
			let _this=this;
			return new Promise((res, rej) => {
				// 请求数据逻辑
				setTimeout(function() {
					_this.scrollTop = _this.top;
					uni.hideLoading();
					res(e);
				}, 2000);
			});
		}
	}
};
</script>

<style>
@keyframes myfirst {
	from {
		top: -100upx;
	}
	to {
		top: 0;
	}
}
.content {
	position: fixed;
	width: 100%;
	height: 100%;
	text-align: center;
	background: #f2f2f5;
}
.uni-swiper-tab {
	background: #ffffff;
	border-bottom: 1px solid #f5f5f5;
}
.swiper-tab-list {
	flex: 1;
	width: 50%;
	padding: 10upx 0;
	box-sizing: border-box;
	letter-spacing: 5upx;
	text-indent: 1upx;
	display: inline-block;
	align-items: center;
}
.displayDiv {
	position: fixed;
	top: -100upx;
	z-index: 99;
	background: #fff;
	width: 100%;
	margin-top: 0 !important;
}
.tab-content {
	font-size: 32upx;
	border-bottom: 2px solid #ffffff;
	width: 50%;
	margin: 0 auto;
}
.active {
	font-size: 36upx;
	font-weight: 600;
	border-bottom: 2px solid #008395;
	color: #008395;
}
.uni-swiper-tab {
	display: flex;
	margin-top: 20upx;
}

.topnav {
	animation: myfirst 0.2s;
	top: 0;
}
</style>



